<template>
	<div id="indexheader">
		<div class="head">
			<div class="logo">
				<img src="../assets/logo.png" />
				<p>Wayne.</p>
			</div>
			<div class="nav">
				<router-link to="/Home">Home</router-link>
				<router-link to="/Category">Category</router-link>
				<router-link to="/Hot">Hot</router-link>
				<router-link to="/New">New</router-link>
			</div>
			<div class="icon">
				<font-awesome-icon :icon="['fas', 'user']" size="lg"/>
				<font-awesome-icon :icon="['fas', 'shopping-bag']" size="lg" @click="toggleShoppingBag"/>
				<font-awesome-icon :icon="['fas', 'search']" size="lg" @click="toggleSearchBar"/>
			</div>
			<div class="menu">
				<font-awesome-icon :icon='["fas", "bars"]' size="2x" @click="toggleMenu"></font-awesome-icon>
			</div>
		</div>
		<div class="search trans05" :class="{ hide: this.ttt }">
			<input type="text" name="key" id="key" value="" placeholder="Enter key word(s)" />
		</div>
	</div>
</template>

<script>
	import bus from "../bus.js";

	export default {
		name: "indexheader",
		data() {
			return {
				ttt: true,
			};
		},
		methods: {
			toggleSearchBar: function() {
				this.$data.ttt = !this.$data.ttt;
			},
			toggleShoppingBag: function() {
				bus.$emit("toggleShoppingCart");
			},
			toggleMenu: function() {
				bus.$emit("closeShoppingCart");
				bus.$emit("toggleMenu");
			}
		},
	};
</script>

<style scoped>
	@import url("../assets/roots.css");

	#indexheader {
		background: darkturquoise;
	}

	.head,
	.nav {
		height: 5rem;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.logo {
		display: flex;
		align-items: center;
		margin-right: 10.25rem;
	}

	.logo img {
		height: 3rem;
	}

	.logo p {
		user-select: none;
		margin: 0;
		padding: 0;
		display: inline-block;
		font-family: helvetica;
		font-weight: bolder;
		font-size: 1.5rem;
		text-transform: capitalize;
		transition: 0.2s ease;
	}

	.logo p:hover {
		color: var(--color-light);
	}

	.nav {
		width: auto;
	}

	.nav a {
		user-select: none;
		font-weight: bolder;
		text-transform: uppercase;
		line-height: 5rem;
		padding: 0 1rem;
		text-decoration: none;
		color: #fff;
		box-sizing: border-box;
		transition: 0.2s ease;
	}

	.nav a:hover {
		background: darkcyan;
	}

	.nav a::after {
		display: block;
		content: "";
		height: 2px;
		width: 100%;
		margin-top: -2px;
		background: var(--color-light);
		transition: 0.2s ease;
	}

	.nav a:hover::after {
		height: 4px;
		margin-top: -4px;
		background: var(--color-light);
	}

	.icon {
		margin-left: 10.25rem;
	}

	.icon svg,
	.menu svg {
		cursor: pointer;
		margin-right: 10px;
		color: var(--color-light);
		transition: 0.2s ease;
	}

	.icon svg:last-of-type,
	.menu svg:last-of-type {
		margin: 0;
	}

	.icon > svg:hover,
	.menu > svg:hover {
		color: var(--color-dark);
	}

	.search {
		height: 64px;
		display: flex;
		overflow: hidden;
	}

	.search.hide {
		height: 0;
	}
	
	.menu {
		display: none;
	}

	#key {
		position: relative;
		border: none;
		background: transparent;
		border-bottom: var(--color-light) 1px solid;
		color: #fff;
		outline-style: none;
		text-align: center;
		font-size: 20px;
		padding: 5px;
		margin: 20px auto 10px;
	}

	#key:focus {
		border-bottom: var(--color-light) 1px solid;
	}

	#key::placeholder {
		color: #f1f2f3;
	}
	
	/* 4 | 5 | SE */
	@media (max-width:375px) {
		#indexheader{
			width: 100%;
		}
		
		.head {
			width: 100%;
			margin: 0 auto;
			justify-content: space-between;
		}
		
		.logo,
		.icon {
			margin: 0;
		}
		
		.head {
			width: 90%;
		}
		
		.nav,
		.icon {
			display: none;
		}
		
		.menu {
			display: block;
		}
		
	}
	
	/* 6 | 7 | 8 | X | Plus | Max */
	@media (min-width: 376px) and (max-width: 568px) {
		#indexheader{
			width: 100%;
		}
		
		.head {
			width: 90%;
			margin: 0 auto;
			justify-content: space-between;
		}
		
		.logo,
		.icon {
			margin: 0;
		}
		
		.nav,
		.icon {
			display: none;
		}
		
		.menu {
			display: block;
		}
	}
	
	/* iPad iPad Pro */
	@media (min-width: 569px) and (max-width:1024px) {
		#indexheader{
			width: 100%;
		}
		
		.head {
			width: 90%;
			margin: 0 auto;
			justify-content: space-between;
		}
		
		.logo,
		.icon {
			margin: 0;
		}
	}
	
</style>



